import Settings from "../settings/static-settings";
var Info = /** @class */ (function () {
    function Info() {
    }
    Info.prototype.render = function () {
        var div = "<div id=\"info\" class=\"info\">You end the game, your score: " + Settings.scorePoints + "<input type=\"submit\" class=\"againButton\" value=\"Play again\"></div>";
        document.body.children[1].innerHTML = div;
    };
    return Info;
}());
export default Info;
